button {
  padding: 15px;
  background-color: #135eb9;
  margin: 15px 0px;
  color: #E1E1E1;
  font-style: bold;
  border-radius: 1px 20px;
}

.altBackground {
  background-color: #adadad;
  color: #0D0A1C;
  margin: 0 -9999rem;
  /* add back negative margin value */
  padding: 0.25rem 9999rem;
}